<template>
    <div class="kotva">
        <h3>{{ getName($route.params.name) }}</h3>
        <h4>ty vyjebaná kotva</h4>
        <img src="/static/LUL.jpg" alt="LUL">

        <iframe width="1" height="1" src="https://www.youtube.com/embed/OxUZ5ZYpdN0?rel=0&amp;controls=0&amp;showinfo=0&amp;autoplay=1&amp;start=15" frameborder="0" allowfullscreen autoplay></iframe>
    </div>
</template>

<script>
    export default {
        methods: {
            getName: function (name) {
                name = name.split('-').map(function (name) {
                    return name.charAt(0).toUpperCase() + name.slice(1);
                });

                return name.join(' ');
            }
        }
    }
</script>

<style lang="scss">
    .kotva {
        margin-top: 50px;
        text-align: center;

        h3 {
            font-size: 5rem;
            color: #6441a4;
            font-weight: 500;
            animation: blinker 1s linear infinite;
        }

        h4 {
            font-weight: 300;
            text-transform: uppercase;
            font-size: 2rem;
            animation: blinker 1s linear infinite;
        }

        img {
            height: 300px;
            margin-top: 20px;
        }
    }

    @keyframes blinker {
        50% { opacity: 0.0; }
    }
</style>



