<template>
    <div v-on:click="openGoldenKappa()" class="golden-kappa">
        <img src="/static/kappa.png" alt="Kappa" title="Golden Kappa sútaž">
    </div>
</template>

<script>
    export default {
        methods: {
            openGoldenKappa: function () {
                window.open('http://golden-kappa.online', 'Golden Kappa', 'toolbar=no ,location=0, status=no, titlebar=no, menubar=no, width=350px, height=350');
            }
        }
    }
</script>

<style lang="scss">
    .golden-kappa {
        position: fixed;
        z-index: 9999;
        bottom: -35px;
        right: -10px;
        cursor: pointer;
        transform: rotate(-35deg);

        img {
            width: 80px;
        }
    }
</style>



