<template>
    <div>
        <h2>Zisti či je tvoja obľúbená <strong>kotvička</strong> online.</h2>

        <section class="buttons">
            <a v-on:click="addStreamer()">
                <button>
                    <i class="fa fa-anchor" aria-hidden="true"></i>
                    Pridaj kotvičku
                </button>
            </a>

            <a v-on:click="generate()">
                <button>
                    <i class="fa fa-plus-circle" aria-hidden="true"></i>
                    Vygeneruj kotva stránku
                </button>
            </a>
        </section>

        <section class="generated" v-if="generated">
            URL stránka pre kotvu je: <span>{{ generated }}</span>
        </section>

        <section class="row">
            <streamer v-for="streamer in streamers" :twitch="streamer"></streamer>
        </section>
    </div>
</template>

<script>
    import Streamer from '../Streamer.vue';
    import Storage from '../../storage.js';

    export default {
        components: {
            Streamer
        },

        data() {
            return {
                streamers: this.getStreamers(),
                generated: false
            }
        },

        methods: {
            getStreamers: function () {
                return Storage.getStreamers();
            },

            addStreamer: function () {
                var streamer = prompt('Channel streamera');

                if (streamer) {
                    Storage.addStreamer(streamer);
                }

                this.streamers = Storage.getStreamers();
            },

            generate: function () {
                var name = prompt('Meno kotvy bez diakrity');

                if (name) {
                    name = name.toString().toLowerCase()
                            .replace(/\s+/g, '-')
                            .replace(/[^\w\-]+/g, '')
                            .replace(/\-\-+/g, '-')
                            .replace(/^-+/, '')
                            .replace(/-+$/, '');

                    this.generated = 'http://kotva.online/#!/show/' + name;
                }
            },
        },
    }
</script>

<style lang="scss">
    .row {
        display: flex;
        max-width: 980px;
        margin: 0 auto;
        flex-wrap: wrap;
    }

    h2 {
        font-weight: 300;
        font-size: 1.2rem;
        text-align: center;
        margin-bottom: 25px;

        strong {
            font-weight: 500;
        }
    }

    .buttons {
        text-align: center;
        margin-bottom: 35px;

        button {
            font-size: .8rem;
        }
    }

    .generated {
        text-align: center;
        margin: 50px 0;

        span {
            color: orange;
            font-size: 1.1rem;
        }
    }
</style>



