var methods = {
    getStreamers: function () {
        var streamers = localStorage.getItem('streamers');

        return streamers ? JSON.parse(streamers) : ['resttpowered', 'zolik22', 'grindererr', '2sekundovymato', 'tvdevi'];
    },

    addStreamer: function (streamer) {
        var streamers = this.getStreamers();

        streamers.push(streamer);

        return this.setStreamers(streamers);
    },

    removeStreamer: function (streamer) {
        var streamers = this.getStreamers(),
            index = streamers.indexOf(streamer);

        if (index > -1) {
            streamers.splice(index, 1);
        }

        return this.setStreamers(streamers);
    },

    setStreamers: function (streamers) {
        localStorage.setItem('streamers', JSON.stringify(streamers));

        return this;
    }
};

export default methods;


