<template>
    <div class="streamer">

        <div class="logo">
            <img v-bind:src="logo" alt="Photo" v-if="logo != ''">
        </div>

        <div class="details">
            <h3>{{ twitch }}</h3>

            <div class="list-item">
                <div class="label">Status:</div>
                <div class="content" v-bind:class="{ 'online': status, 'offline': !status }">
                    {{ status ? 'Online' : 'Offline' }}
                </div>
            </div>

            <div class="list-item" v-if="status">
                <div class="label">Hra:</div>
                <div class="content">
                    {{ game }}
                </div>
            </div>

            <div class="list-item" v-if="status">
                <div class="label">Sleduje:</div>
                <div class="content">
                    {{ viewers }} ľudí
                </div>
            </div>

            <div v-if="status" class="buttons">
                <a v-link="{ name: 'watch', params: { stream: twitch } }">
                    <button type="button"><i class="fa fa-twitch" aria-hidden="true"></i> HTML5 Stream</button>
                </a>
            </div>
        </div>

        <div class="close">
            <span v-on:click="removeStreamer(twitch)">&times;</span>
        </div>

    </div>
</template>

<script>
    import Storage from '../storage.js';

    export default {
        props: ['twitch'],

        data() {
            return {
                status: false,
                game: '',
                viewers: 0,
                logo: ''
            }
        },

        methods: {

            getStreamInfo: function () {

                this.$http.get('https://api.twitch.tv/kraken/streams/' + this.twitch)
                        .then(function (response) {
                            var stream = response.body.stream;

                            if (stream) {
                                this.status = true;
                                this.game = stream.game;
                                this.viewers = stream.viewers;
                                this.logo = stream.channel.logo;
                            } else {
                                this.getChannelInfo();
                            }
                        })
                        .catch(function (error) {
                            console.error(error);
                        });


                window.setTimeout(this.getStreamInfo, 5000);
            },

            getChannelInfo: function () {
                this.$http.get('https://api.twitch.tv/kraken/channels/' + this.twitch)
                        .then(function (response) {
                            this.logo = response.body.logo;
                        })
                        .catch(function (error) {
                            console.error(error);
                        });
            },

            removeStreamer(streamer) {
                Storage.removeStreamer(streamer);

                this.$parent.streamers = Storage.getStreamers();
            }
        },

        created() {
            this.getStreamInfo();
        }
    }
</script>

<style lang="scss">
    .streamer {
        flex-basis: calc(50% - 15px / 2);
        background: #1e1e1e;
        border: 1px solid #333;
        border-radius: 6px;
        padding: 15px;
        display: flex;
        vertical-align: center;
        justify-content: center;
        text-shadow: 2px 2px 5px #333;
        margin-bottom: 15px;

        @media screen and (max-width: 680px) {
            flex-basis: 100% !important;
            margin-right: 0 !important;
        }

        a, a:hover {
            text-decoration: none;
        }

        .logo {
            flex-basis: 40%;
        }

        .details {
            flex-basis: 58%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
        }

        .close {
            flex-basis: 2%;

            span {
                color: red;
                opacity: .5;
                font-size: 1.2rem;
                cursor: pointer;

                &:hover {
                    opacity: 1;
                }
            }
        }

        img {
            width: 100px;
            height: 100px;
            border-radius: 50%;
            margin-top: 20px;
        }

        .list-item {
            display: flex;
            width: 100%;

            .label {
                flex-basis: 35%;
            }

            .content {
                flex-basis: 65%;
            }
        }

        h3 {
            text-transform: uppercase;
            font-weight: 500;
            color: #6441a4;
        }

        .online {
            color: green;
        }

        .offline {
            color: red;
        }

        .buttons {
            margin-top: 10px;
        }
    }

    .streamer:nth-child(odd) {
        margin-right: 15px;
    }
</style>


