import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';

import App from './App.vue';
import Home from './components/pages/Home.vue';
import Kotvicka from './components/pages/Kotvicka.vue';
import Watch from './components/pages/Watch.vue';

Vue.use(VueResource);
Vue.use(VueRouter);

export var router = new VueRouter();

router.map({
    '/home': {
        name: 'home',
        component: Home
    },
    '/watch/:stream': {
        name: 'watch',
        component: Watch
    },
    '/show/:name': {
        name: 'show',
        component: Kotvicka
    }
});

router.redirect({
    '*': '/home'
});

router.start(App, 'body');


